@import url('https://fonts.googleapis.com/css2?family=Acme&display=swap');
.text{
    text-align: center;
    color: rgb(246, 242, 242);
    font-size: 10vh;
    padding: 65px;
    text-shadow: rgb(52, 12, 212) 1px 0 10px;
    font-weight: 150;
    font-family: 'Acme', sans-serif;
    background-color: rgba(1, 9, 15, 0.597);
    line-height: 60px;
    transition: all 0.3s ease;
}
.hereismytext{
    font-size: 18px;
    padding: 0px;
    margin: 0px;
    font-family: 'Courier New', Courier, monospace;
    transition: all 0.3s ease;
}
.physics{
    color: #17ff4d;
}
.p1{
    display: inline-block;
    padding: 2px;
    transition: all 0.3s ease;
}
.instalogo{
    width: 60px;
    margin-top: 40px;
    margin-right: 30px;
    border-radius: 55px;
}
.instalogo:hover{
    width: 75px;
    box-shadow: 0 0 20px #d70909;
    border-radius: 55px;
    transition: all 0.4s ease;
}
.facelogo{
    width: 60px;
    margin-top: 40px;
    margin-right: 30px;
    border-radius: 55px;
}
.facelogo:hover{
    width: 75px;
    box-shadow: 0 0 20px #0627ff;
    border-radius: 55px;
    transition: all 0.4s ease;
}