.collegeline{
    color: aliceblue;
    text-align: center;
    font-size: 40px;
}
.smallcontainer{
    background-color: black;
    margin: 30px;
    padding: 20px;
}

.iitrimage {
    width: 100%;
    padding-top: 20px;
    height: 400px;
    max-height: fit-content;
    transition: transform 3s ease; /* Adding a transition for smooth animation */
}

.iitrimage:hover {
    transform: rotate(30deg); /* Rotate the image by 20 degrees on hover */
    transition: transform 3s ease;
}


