@import url('https://fonts.googleapis.com/css2?family=Acme&display=swap');

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body {
  background-image: url(https://bestanimations.com/media/stars/985783246stars-sky-space-gif-26.gif);
  background-size: cover;
  background-position: center;
  height: 100vh;
  background-attachment: fixed;
}

header {
  background-color: rgb(0, 0, 0);
  font-family: 'Acme', sans-serif;
  width: 100%;
  z-index: 999;
  line-height: 65px;
}

.wholediv {
  display: flex;
  justify-content: space-between;
  margin: auto;
  align-items: center;
  padding-inline: 60px 30px;
}

.img1 {
  border-radius: 50%;
}

.logo h2 a {
  color: aliceblue;
  text-decoration: none;
  font-size: xx-large;
  padding: 0vw;
  padding-top: 5px;
}

.logo {
  display: flex;
  width: 20px;
  height: 80px;
  margin-inline-end: 2vw;
}

.navbarlistname a {
  color: aliceblue;
  text-decoration: none;
  padding: 9px 2vw;
  font-size: 18px;
  font-weight: 100;
  border-radius: 5px;
  transition: all 0.3s ease;
}

.navbarlistname a:hover {
  background-color: rgba(0, 0, 0, 0.181);
  font-size: 24px;
  color: rgb(206, 0, 229);
  text-decoration: none;
}

.wholediv .lihead {
  display: inline-flex;
}

.drop-menu {
  display: none;
}

#drop:hover .drop-menu {
  display: block;
  position: absolute;
  z-index: 1000;
  background: rgb(0, 0, 0);
  transition: all 1s ease;
}

.rocket {
  width: 60px;
  border-radius: 50%;
}

button {
  padding: 10px 30px;
  border: 2px solid black;
  border-radius: 20px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 20px;
}

button a {
  text-decoration: none;
  color: black;
}

button:hover {
  background-color: rgb(255, 33, 255);
  transform: scale(1.2, 1.2);
  transition: all 0.3s ease;
}

#drop:hover {
  backdrop-filter: blur(1000px);
}

/* Media query for smaller screens */
@media (max-width: 768px) {
  .wholediv {
    flex-direction: column;
    align-items: center;
  }

  .logo {
    margin-bottom: 20px;
  }

  .navbarlistname {
    padding: 10px;
  }

  .drop-menu {
    position: static;
    display: none;
    background: transparent;
    transition: none;
    box-shadow: none;
  }

  .drop-menu li {
    text-align: center;
    margin: 10px 0;
  }

  #drop:hover .drop-menu {
    display: block;
    background: rgb(0, 0, 0);
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  }

  .btn {
    margin-top: 20px;
  }
}
