@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap');
.middlecontainer{
    display: grid;
    width: 100%;
    height: 500px;
    grid-template-columns: repeat(2,1fr);
    row-gap: 2px;
    column-gap: 5px;
    box-sizing: border-box;
    border-radius: 20px;

}
/* Media query for smaller screens */
@media (max-width: 768px) {
  .middletext {
    display: none;
  }
}

.middleitem{
    background-color: transparent;
    box-sizing: border-box;
} 
.middleitem1{
    backdrop-filter: blur(15px);
}
.middletext{
    color: rgb(157, 255, 0);
    text-align: center;
    font-size: 30px;
    padding: 30px;
    transition: all 1s ease;
}
.middletext2{
    color: rgb(234, 237, 230);
    text-align: center;
    padding: 30px;
    box-sizing: border-box;
    display: block;
   
}
.middleitem1:hover .middletext{
    font-size: 2.5em;
    transition: all 1s ease-in-out;
}


.book {
    position: absolute;
    background-image: url(https://cdn.dribbble.com/users/1338729/screenshots/5098253/ae_part2.gif);
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 10px;
    width: 50%;
    height: 500px;
    background-color: whitesmoke;
    -webkit-box-shadow: 1px 1px 12px #000;
    box-shadow: 1px 1px 12px #000;
    -webkit-transform: preserve-3d;
    -ms-transform: preserve-3d;
    transform: preserve-3d;
    -webkit-perspective: 2000px;
    perspective: 2000px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    color: #000;
    display: block;
  }
  
  .cover {
    top: 0;
    position: absolute;
    background-image: url(physics.jpg);
    width: 100%;
    height: 100%;
    border-radius: 10px;
    cursor: pointer;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s;
    -webkit-transform-origin: 0;
    -ms-transform-origin: 0;
    transform-origin: 0;
    -webkit-box-shadow: 1px 1px 12px #000;
    box-shadow: 1px 1px 12px #000;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    display: block;
  }
  
  .book:hover .cover {
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    -webkit-transform: rotatey(-80deg);
    -ms-transform: rotatey(-80deg);
    transform: rotatey(-80deg);
  }
  .gridbook{
    width: 100%;
    height:300px;
  }